@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main {
    min-height: 93vh;
    display: flex;
    align-items: center;
    background-image: url(/static/media/studytracker1.528f9ed7.png);
    background-position: center;
    background-size: cover;
}

.title {
    font-size: 95px;
    text-align: center;
}

.subtitle {
    font-size: 25px;
    padding-top: 10px;
}

.intro-text {
    width: 100%;
    text-align: center;
}

.buttonContainer {
    margin-top: 50px;
    display: flex;
    justify-content: space-evenly;
}

.homebutton {
    width: 200px;
    height: 55px;
}

@media only screen and (min-width:1200px){
    .buttonContainer{
        padding: 0 200px;
    }
    .title {
        padding: 0 100px;
    }
}

@media only screen and (max-width: 768px){
    .title {
        font-size: 60px;
    }
    .homebutton {
        width: 150px;
        height: 50px
    }
}
.signinContainer {
    display: flex;
    flex-direction: column;
    margin: 20px;
}
.mainback {
    min-height: 93vh;
    display: flex;
    padding: 10px 0;
}
.page {
    width: 100%;
}

.heading {
    font-size: 60px;
    font-family: 'Nunito', sans-serif;
    padding: 5px 10px;
}
@media only screen and (max-width: 660px) {
    .heading {
        font-size: 30px;
        display: flex;
        justify-content: center;
    }
}
.profileContainer {
    display: flex;
    margin: 20px;
}

@media only screen and (max-width: 768px) {
    .profileContainer {
        flex-direction: column-reverse;
    }
}
